<template>
  <BaseLayout title="分类" nonavbar>

    <van-sticky :offset-top="0" class="toolbar">
      <van-row gutter="0" justify="space-around" align="center">
        <van-col span="5">
          <van-icon :name="logo" size="36" color="#000" @click="$router.push('/')"></van-icon>
        </van-col>
        <van-col span="16">
          <van-search v-model="state.keyword" placeholder="输入关键词搜索更多商品" background="transparent" input-align="left" @search="$router.push('list?keyword=' + state.keyword)" />
        </van-col>
        <van-col span="2"> 
          <van-icon name="cart-o" size="large" :class="state.animate" :badge="profile && profile.cartNumber ? profile.cartNumber : ''" @click="$router.push('cart')"></van-icon>
        </van-col>
        <van-col span="1"></van-col>
      </van-row>
    </van-sticky>

    <van-tree-select class="category" v-model:main-active-index="state.active" :items="state.category" @clickNav="changeNav">
      <template #content>
        <div v-if="state.list[state.cid] && state.list[state.cid].length > 0">
          <mall-item v-for="(item, index) in state.list[state.cid]" :key="index" :item="item" :list="90" :stock="false" :sales="false" :cart="false"></mall-item>
          <van-button :loading="state.list[state.cid].length <= 20" plain block style="border:0" color="#CCC" text="-- 更多商品请在顶部搜索 --" />
        </div>
        <div v-else>
          <van-empty description="暂无商品"></van-empty>
        </div>
      </template>
    </van-tree-select>

  </BaseLayout>
</template>

<style scoped>
.toolbar {
  background: #FFF url('~@/assets/bg_navbar.jpg') repeat bottom center;
  text-align:center;
}
.category {
  min-height: 600px;
}
</style>

<script>
import {reactive} from 'vue'
import {mapState, mapGetters, mapMutations} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'
import MallItem from '@/components/MallItem.vue'

export default {
  components: {BaseLayout, MallItem},

  props: {
    id: {
      type: [String, Number],
      default: 0,
    },
  },

  data() {
    let state = reactive({
      active: 0,
      cid: 0,
      category: [],
      list: [],
      keyword: "",
    });
    return {state};
  },

  created(){
    this.$http.get("/jdmall/query?method=product/getPageNum").then((data) => {
      let arr = _.map(data, (obj) => {
        obj["id"] = parseInt(obj["page_num"]);
        obj["text"] = obj["name"];
        return obj;
      })
      arr = _.sortBy(arr, "id");
      this.state.category = arr;

      this.state.active = _.findIndex(arr, (o) => {return o.id == this.id});
      this.state.active == -1 && (this.state.active = 0);
      this.changeNav();
    });
  },

  computed: {
    logo (){
      return require("../../assets/jdlogo.png");
    },
    ...mapState(["profile"]),
  },

  methods: {
    changeNav() {
      this.state.cid = this.state.category[this.state.active]["id"];
      this.$router.replace("/jdmall/category?id=" + this.state.cid);
      this.query(this.state.cid);
    },

    query(id){
      if (typeof this.state.list[id] != "undefined")
        return;
      this.state.list[id] = [];
      this.$http.post("/jdmall/query?method=product/getSkuByPage&pageNum=" + this.state.cid).then((data) => {
        let arr = _.sampleSize(_.slice(data.skuIds, 0, 100), 30);
        _.forEach(arr, (v) => {
          this.$http.get("/jdmall/item?id=" + v).then((item) => {
            this.state.list[id].push(item);
          });
        });
      });
    },
  }
}
</script>
